.auth__logo{
    font-family: Cabin,sans-serif;
    font-size: 20px;
    font-weight: 700;
    color: #1d4289;
}
.auth__header{
    padding-top: 14px;
    padding-bottom: 14px;
}
.auth{
    overflow: auto;
    background: #F4F7FF;
    h1{
        font-weight: bold;
        font-size: 48px;
        line-height: 52px;
        color: #0A243F;
        text-align: center;
        margin-bottom: 42px;
    }
    .auth__desc{
        font-size: 24px;
        line-height: 36px;
        letter-spacing: -0.015em;
        color:#022964;
        text-align: center;
        max-width: 1211px;
        display: block;
        margin: 0 auto;
    }   
}

@media(min-width: 1400px){
    .container--auth{
        max-width: 1550px;
    }
}

.auth__form{
    background: #FFFFFF;
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.15);
    border-radius: 15px;
    position: relative;
    padding: 60px 63px 82px;
    max-width: 622px;
    width: 100%;
    margin: 0 auto;
    margin-top: 71px;
    margin-bottom: 36px;
}
.auth__form__close{
    display: block;
    position: absolute;
    top: 5px;
    right: 5px;
    padding: 5px;
    background-color: transparent;
    border: none;
    transition: 0.3s all;
    div{
        width: 20px;
        height: 20px;
    }
    svg{
        display: block;
        width: 20px;
        height: 20px;
        path{
            transition: 0.3s all;
        }
    }
}
.auth__form__close:hover svg{
    transform: scale(1.05);
}
.auth__form__title{
    font-weight: bold;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: -0.015em;
    color: #022964;
    margin-bottom: 40px;
}
.input__row{
    margin-bottom: 24px;
    input{
        width: 100%;
        background: #FFFFFF;
        border: 1px solid #D7D6DC;
        box-sizing: border-box;
        box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
        border-radius: 20px;
        height: 40px;
        padding: 0 15px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.015em;
        color: #262A39;
    }
    input::placeholder{
        color: #79758A;
    }
    input:focus{
        border: 2px solid #2B5CFB;
    }
}
.input__row--pass{
    position: relative;
    button{
        padding: 0;
        width: 24px;
        border: 0;
        background-color: transparent;
        height: 24px;
        position: absolute;
        right: 12.5px;
        top: 50%;
        transform: translateY(-50%);
        svg{
            width: 100%;
            height: 100%;
            fill: #615E6E;
        }
        .show{
            display: none;
        }
        .hide{
            display: block;
        }
    }
    button.active{
        .show{
            display: block;
        }
        .hide{
            display: none;
        }
    }
}

.checkbox{
    display: flex;
    align-items: flex-start;
    font-size: 10px;
    line-height: 10px;
    letter-spacing: -0.015em;
    color: rgba(0, 0, 0, 0.8);
    padding-top: 5px;
    margin-bottom: 28px;
    a{
        color: #1D83E2;
        text-decoration: underline;
    }
    a:hover{
        color: #155ea3;
    }
}
.checkbox label{
    transform: translateY(-5px);
    margin-right: 11px;
    cursor: pointer;
    input{
        display: none;
    }
    input + span{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 18px;
        height: 18px;
        border: 2px solid #AFACB9;
        svg{
            opacity: 0;
            transition: 0.3s all;
        }
    }
    input:checked + span{
        border-color: #000;
        svg{
            opacity: 1;
        }
    }
}

.auth__submit{
    background: #0F47C0;
    border-radius: 20px;
    font-weight: bold;
    font-size: 20px;
    line-height: 32px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: -0.015em;
    color: #FFFFFF;
    padding: 4px 15px;
    width: 100%;
    border: 0;
    justify-content: center;
    transition: 0.3s all;
    margin-bottom: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
}
.auth__submit:hover{
    background-color: #092C9E;
}
.auth__form__text{
    font-size: 12px;
    line-height: 20px;
    /* identical to box height, or 167% */
    margin-bottom: 4px;
    letter-spacing: -0.015em;
    color: #000000;
    a{
        color: #1D83E2;
        text-decoration: underline;
    }
    a:hover{
        color: #155ea3;
    }
}
.auth__form__text--right{
    text-align: right;
}
.auth__reg-width{
    margin-top: 16px;
}
.auth__reg-width a{
    background: #0F47C0;
    border-radius: 20px;
    font-size: 14px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: -0.015em;
    color: #FFFFFF;
    padding: 4px 15px;
    width: 100%;
    border: 0;
    justify-content: center;
    transition: 0.3s all;
    margin-top: 12px;
    position: relative;
    height: 40px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
    
    .img{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 4px;
        width: 32px;
        display: flex;
        justify-content: center;
        
    }
    .img.white{
        background-color: #fff;
        border-radius: 100%;
    }
    
}
a.auth__reg-width__google{
    background-color: #141A66;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
}
a.auth__reg-width__google:hover{
